  import Scroll from "../../base/scroll/scroll.vue"
  import Step from "../../base/step/step.vue"
  import Upload from "../../base/upload/upload.vue"

  export default {
    name: "getPrice1",
    components: {
      Step,
      Upload,
      Scroll
    },
    data() {
      return {
        isOk: true, //
        goodsImg: [],
        msgCont: '',
        twoInfo: {}
      }
    },
    //    
    created() {
      //var buyerTaskRecordId = localStorage.getItem("buyerTaskRecordId");
      var that = this;
      this.$axios.post('/api/orderOperate/getBuyerTask', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
      }).then((res) => {
        console.log(res)
        let data = res.data
        if (data.code === "200") {
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      });

      //
      this.$axios.post('/api/orderOperate/getAdditionalInfo', {
        buyerTaskRecordId: that.$route.query.buyerTaskRecordId
      }).then((res) => {
        let data = res.data
        if (data.code === "200") {
          this.twoInfo = data.data
        }
      }).catch((error) => {
        this.$vux.alert.show({
          title: '',
          content: '',
        })
      })
    },
//    
    methods: {
      next() {
        var that = this;
        //var buyerTaskRecordId = localStorage.getItem("buyerTaskRecordId");
        this.$axios.post('/api/orderOperate/backOrderSubmit', {
          buyerTaskRecordId: that.$route.query.buyerTaskRecordId,
          goodsImg: that.goodsImg
        }).then((res) => {
          if (res.data.code === '200') {
            this.$router.push({ name: 'submitSuccess', query: { type: 'evaluate1' }})
          }
        }).catch((error) => {
          this.$vux.alert.show({
            title: '',
            content: ',',
          })
        })
      }
    }
  }



